@import url(https://fonts.googleapis.com/css?family=Baskervville|Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

h1 {
  font-family: 'Baskervville', serif;
}

h2, .footer_center{
  font-family: 'Raleway', sans-serif;
}
form{
  text-align: left;
}
.form-label{
  font-family: 'Raleway', sans-serif;
  font-size: 1.5em;
}

.navbar {
    background-color:#F2D5CE;
    
}
.header-text{
    margin-top: 7%;
    margin-bottom: 7%;
    display: block;
   
}
.box-row{
    width: 90%;
    margin: auto;
}
.box{
    height: 30vh;
    display: flex;
    align-items: center;
    justify-content: center;
    border: solid  #D7D7D7 1px !important;
}

.box:hover{
    background-color:#F2D5CE;
    border: solid black 1px !important;
}

a, a:visited, a:hover, a:active{
    color: black;
    text-decoration: none;
   
}

.bottom-row{
    margin-bottom: 5%;
}



/* Portrait */
@media only screen 
  /* and (min-device-width: 768px)  */
  and (max-device-width: 1024px) 
  and (orientation: portrait) 
  and (-webkit-min-device-pixel-ratio: 1) {
    .header-col-1, .header-col-2{
        flex:50 1;
    }
}

.form-wrapper{
    margin-top: 5%;
    margin-bottom: 5%;
}
.question-form{ 
    margin:auto;
    border: 1px solid #F2A88D;
    padding: 20px;
}
.form-group{
    margin-bottom: 2rem;
}

.dropdownHeading{
    font-family: 'Raleway', sans-serif;
    font-size: 1.5em;
}

.buttonWidth{
    width:100%;

}

.btn{
    font-size: 1.1rem;
}
.btn-primary, .btn-primary:visited, .btn-primary:focus, .show>.btn-primary.dropdown-toggle, .show>.btn-primary.dropdown-toggle:focus{
    color: black!important;
    background-color: #F2A88D!important;
    border-color:  #F2A88D!important;
    box-shadow: #F2A88D!important;
}


.btn-primary:hover, .btn-primary:focus{
    color: black;
    background-color:white;
    border-color:  #F2A88D!important;
    box-shadow:  #F2A88D!important;
}

.submitButton{
    font-size: 1.5rem;
    color: white!important;
    background-color:#4A738C!important;
    border-color: #4A738C!important;
   
}

.colorPicker{
    width: auto!important;
}

.results-container{
    margin: 4%;
}


.sticker, .itemDiv>p{
    z-index:50;
    /* background-color: #F2A88D; */
    opacity: .8;  
    position: absolute;
    top: 5%;
    right: 5%;
    width: 50px;
    height: 50px;
    line-height: 50px;
    border: solid #F2A88D 1px;
    border-radius: 100%;
}

.pickedItem{
    background-color: #F2A88D;
}
.carousel-caption p{
    color:black;
    font-size: .8rem;

    background-color:#F2D5CE;
    padding: 0;
    margin: 0;
    opacity: .8;
}

.itemPrice {
    margin-top:5px;
}

.itemPrice{
    font-weight: bolder;
}
.carousel-caption{
    bottom: 0;
    padding-top: 0;
    padding-bottom: 0;
}

.carousel-indicators{
    display: none;
}

.itemDiv{
    height: 44vh;
}


.carousel-item.active{
    display: flex;
}

.col-sm{
    border: solid #D7D7D7 1px;
}

.itemDiv .w-100{
    width: auto!important;
    max-width: 75%;
    max-height: 100%;
    margin: auto!important;
}

.carousel-control-prev-icon, .carousel-control-next-icon {
  background-size: 100%, 100%;
  background-image: none;
 
}
.carousel-control-next-icon:after
{
  content: '>';
  font-size: 30px;
  color:#F2A88D;
}

.carousel-control-prev-icon:after {
  content: '<';
  font-size: 30px;
  color:#F2A88D;
}

.wardrobe-summary{
    text-align: left!important;
    font-family: 'Raleway', sans-serif;
}

.wardrobe-summary h4{
    font-family: 'Baskervville', serif;
}

.picks{
    border: none;
}

.total{
    margin-left: 10px;
    margin-bottom: 10px;
    background-color: #F2A88D; 
    border: solid #F2A88D 1px;
    font-size: 1.5rem;
}

.total:hover{
    color: white; 
}

.wardrobe{
    margin-left: 10px;
    background-color: #4A738C;
    border-color: #4A738C;
    height: 50px;
    font-size: 2rem;
    color: white;
}

.wardrobe:hover{
    background-color:#4A738C;
    border-color: #7BB38B!important;
    /* border-color: #4A738C!important; */
    color: black;
}

/* .wardrobe-summary>h5{
    background-color:  #D7D7D7;
} */
.footer {
    padding:20px;
    background-color:#F2D5CE;
    font-family: 'Raleway', sans-serif;
}

/* .footer p {
    line-height: 40px;
} */

.social-media-icon img{
    width: 22%;
    padding-left:5px;
   
}

@media screen and (max-width: 600px) {
    .social-media-icon img{
        width: 15%;
       
       
    }
  }
